.counter-box {
    padding: 20px;
    border: 3px solid blue;
    margin-top: 20px;
    width: 160px;
    height: 150px;
    text-align: center;
    border-radius: 10px;
    display: inline-block;
}

.counter-num{
    display: block;
    color: blue;
}

.counter-butt {
    display: inline-block;
    border: 2px solid grey;
    padding: 5px;
    background-color: white;
    border-radius: 10px;
}

.ml-15 {
    margin-left: 15px;
}

.mr-15 {
    margin-right: 15px;
}

.mt-10 {
    margin-top: 10px;
}

.grey {
    color: rgb(56, 56, 56);
}

.counter-list {
    margin-top: 15px;
}